.header {
    width: 296px;
    height: 100vh;
    padding: 32px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.admin-logo {
    margin: 0 0 32px 32px;
}

.admin-menu {
    font-weight: 700;

    li {
        width: inherit;
        margin-bottom: 26px;

        a {
            padding: 6px 0 6px 32px;
            border-left: 4px solid $grey-100;
            vertical-align: middle;
            
            &:hover {
                color: $link-hover;
                border-left: 4px solid $link-hover;
            }

            span {
                width: 24px;
                height: 24px;
                vertical-align: middle;
                margin-right: 16px;
            }
        }

    }

}

.user-info {
    margin-left: 32px;
    display: flex;
    align-items: center;
}

.user-img {
    width: 40px;
    height: 40px;
    margin-right: 16px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;

    img {
        width: 100%;
        position: absolute;
        top: -5px;
    }
}

.user-account {
    p {
        margin-bottom: 0;

        &:first-child {
            font-weight: 700;
            color: $grey-900;

        }

        &:last-child {
            font-size: 14px;
            color: $grey-600;
        }
    }
}